import './style.css';

import App from './controllers/App';

class StartPage {
  constructor() {
    this.searchLine = document.createElement('input');
  }

  buildForm() {
    this.searchLine.id = 'search-line';
    this.searchLine.type = 'text';
    this.searchLine.autofocus = 'autofocus';
    this.searchLine.placeholder = ' ';
  }

  buildStartPage() {
    document.body.insertBefore(this.searchLine, document.querySelector('script'));
    document.body.firstElementChild.addEventListener('keyup', (e) => {
      const kcode = 13;
      if (e.keyCode === kcode) {
        e.preventDefault();
        if (document.querySelector('.slider')) {
          document.body.removeChild(document.querySelector('.slider'));
          document.body.removeChild(document.querySelector('.nav'));
        }
        const searchValue = document.body.firstElementChild.value;
        const finalRequest = searchValue.match(/\S+/gi).join('+');
        const app = new App(15, finalRequest);
        app.start();
      }
    });
  }
}

const startPage = new StartPage();

startPage.buildForm();

startPage.buildStartPage();
