import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor(amount, request) {
    this.requestAmount = amount;
    this.searchState = {
      dataURL: `https://www.googleapis.com/youtube/v3/search?key=AIzaSyBNrhOmA618utdzC5TUacZ-QtK7ZbBG26I&type=video&part=snippet&maxResults=${this.requestAmount}&q=${request}`,
    };
  }

  async start() {
    const model = new AppModel(this.searchState, this.requestAmount);
    const data = await model.getClipData();
    const view = new AppView(await data);

    view.render();
  }
}
