import AppNext from './AppNext';

export default class AppSlider {
  constructor(data) {
    this.data = data;
  }

  static getMaxPage(dataLength) {
    const responceClipAmount = AppSlider.getResponceClipAmount();
    const maxPage = Math.ceil(dataLength / responceClipAmount);
    return maxPage - 1;
  }

  static getResponceClipAmount() {
    let responceClipAmount;
    const bodyWidth = parseInt(document.body.offsetWidth, 10);
    if (bodyWidth > 1024) {
      responceClipAmount = 4;
    } else if (bodyWidth <= 1024 && bodyWidth > 768) {
      responceClipAmount = 3;
    } else if (bodyWidth <= 768 && bodyWidth > 480) {
      responceClipAmount = 2;
    } else responceClipAmount = 1;
    return responceClipAmount;
  }

  addSliderView() {
    /* ------------------------------SWIPE------------------------------- */

    const slider = document.querySelector('.slider');
    let leftClipIndex = 0;
    let moveDirection;
    let isDown = false;
    let startX;
    let pageNum = 0;
    const { data } = this;
    let newDataLength = data.length;
    let maxPageNum = AppSlider.getMaxPage(newDataLength);

    slider.onmousedown = (e) => {
      moveDirection = 0;
      if (e.target.tagName !== 'A') {
        startX = e.pageX;
        isDown = true;
      } else;
    };

    slider.onmousemove = (e) => {
      if (!isDown) return;
      e.preventDefault();
      const endX = e.pageX;
      moveDirection = endX - startX;
      document.querySelector('.slider').style = `--clip-amount: ${newDataLength}; --page-num: ${pageNum};  --move-direction: ${moveDirection}px`;
      document.querySelector('.current').innerHTML = pageNum + 1;
    };

    slider.onmouseup = () => {
      isDown = false;
      if (pageNum === 0 && moveDirection < 0) {
        pageNum = 1;
        leftClipIndex = AppSlider.getResponceClipAmount();
      } else if (pageNum !== 0 && moveDirection < 0 && pageNum < maxPageNum) {
        if (pageNum === (maxPageNum - 1) && newDataLength < 45) {
          const searchValue = document.body.firstElementChild.value;
          const finalRequest = searchValue.match(/\S+/gi).join('+');
          newDataLength += 15;
          maxPageNum = AppSlider.getMaxPage(newDataLength);
          const appNext = new AppNext(newDataLength, finalRequest);
          appNext.start();
        }
        if (pageNum < maxPageNum) {
          pageNum += 1;
          leftClipIndex += AppSlider.getResponceClipAmount();
        }
      } else if (pageNum !== 0 && moveDirection > 0) {
        pageNum -= 1;
        leftClipIndex -= AppSlider.getResponceClipAmount();
      } else;
      document.querySelector('.slider').style = `--clip-amount: ${newDataLength}; --page-num: ${pageNum};  --move-direction: 0px`;
      document.querySelector('.current').innerHTML = pageNum + 1;
    };

    slider.ontouchstart = (e) => {
      moveDirection = 0;
      if (e.path[0].tagName !== 'A') {
        startX = e.changedTouches[0].pageX;
        isDown = true;
      } else;
    };

    slider.ontouchmove = (e) => {
      if (!isDown) return;
      e.preventDefault();
      const endX = e.changedTouches[0].pageX;
      moveDirection = endX - startX;
      document.querySelector('.slider').style = `--clip-amount: ${newDataLength}; --page-num: ${pageNum};  --move-direction: ${moveDirection}px`;
      document.querySelector('.current').innerHTML = pageNum + 1;
    };

    slider.ontouchend = () => {
      isDown = false;
      if (pageNum === 0 && moveDirection < 0) {
        pageNum = 1;
        leftClipIndex = AppSlider.getResponceClipAmount();
      } else if (pageNum !== 0 && moveDirection < 0) {
        if (pageNum === (maxPageNum - 1) && newDataLength < 45) {
          const searchValue = document.body.firstElementChild.value;
          const finalRequest = searchValue.match(/\S+/gi).join('+');
          newDataLength += 15;
          maxPageNum = AppSlider.getMaxPage(newDataLength);
          document.querySelector('.slider').style = `--clip-amount: ${newDataLength};`;
          const appNext = new AppNext(newDataLength, finalRequest);
          appNext.start();
        }
        if (pageNum < maxPageNum) {
          pageNum += 1;
          leftClipIndex += AppSlider.getResponceClipAmount();
        }
      } else if (pageNum !== 0 && moveDirection > 0) {
        pageNum -= 1;
        leftClipIndex -= AppSlider.getResponceClipAmount();
      } else;
      document.querySelector('.slider').style = `--clip-amount: ${newDataLength}; --page-num: ${pageNum};  --move-direction: 0px`;
      document.querySelector('.current').innerHTML = pageNum + 1;
    };

    /* -------------------------------BUTTONS------------------------------- */

    const buttonSection = document.createElement('section');
    const toStartButton = document.createElement('button');
    const prevButton = document.createElement('button');
    const currentButton = document.createElement('button');
    const nextButton = document.createElement('button');

    buttonSection.className = 'nav';
    toStartButton.className = 'start';
    prevButton.className = 'prev';
    currentButton.className = 'current';
    currentButton.innerHTML = pageNum + 1;
    nextButton.className = 'next';

    toStartButton.onclick = () => {
      document.querySelector('.slider').style = '--page-num: 0;';
      document.querySelector('.current').innerHTML = 1;
      leftClipIndex = 0;
    };

    prevButton.onclick = () => {
      if (pageNum > 0) {
        pageNum -= 1;
        leftClipIndex -= AppSlider.getResponceClipAmount();
        document.querySelector('.slider').style = `--page-num: ${pageNum}; --clip-amount: ${newDataLength};`;
        document.querySelector('.current').innerHTML = pageNum + 1;
      }
    };

    nextButton.onclick = () => {
      if (pageNum === (maxPageNum - 1) && newDataLength < 45) {
        const searchValue = document.body.firstElementChild.value;
        const finalRequest = searchValue.match(/\S+/gi).join('+');
        newDataLength += 15;
        maxPageNum = AppSlider.getMaxPage(newDataLength);
        document.querySelector('.slider').style = `--clip-amount: ${newDataLength};`;
        const appNext = new AppNext(newDataLength, finalRequest);
        appNext.start();
      }
      if (pageNum < maxPageNum) {
        pageNum += 1;
        leftClipIndex += AppSlider.getResponceClipAmount();
        document.querySelector('.slider').style = `--page-num: ${pageNum}; --clip-amount: ${newDataLength}`;
        document.querySelector('.current').innerHTML = pageNum + 1;
      }
    };

    buttonSection.appendChild(toStartButton);
    buttonSection.appendChild(prevButton);
    buttonSection.appendChild(currentButton);
    buttonSection.appendChild(nextButton);

    document.body.appendChild(buttonSection);

    /* -----------------------------REASSIGN PAGE ON RESIZE-------------------------- */

    const liner = document.createElement('div');
    liner.className = 'liner';
    document.body.appendChild(liner);
    let exClipsPerPage = AppSlider.getResponceClipAmount();
    let clipsPerPage;

    function reassignPage(cPP) {
      pageNum = Math.floor(leftClipIndex / cPP);
      document.querySelector('.slider').style = `--page-num: ${pageNum};`;
      document.querySelector('.current').innerHTML = pageNum + 1;
    }

    function getLeftClipIndex() {
      leftClipIndex = clipsPerPage * pageNum;
      return leftClipIndex;
    }

    window.onresize = () => {
      if ((getComputedStyle(document.querySelector('.liner')).width) === '1025px') {
        clipsPerPage = AppSlider.getResponceClipAmount();
        if (exClipsPerPage !== clipsPerPage) {
          reassignPage(clipsPerPage);
          leftClipIndex = getLeftClipIndex();
          exClipsPerPage = clipsPerPage;
          document.querySelector('.slider').style = `--clip-amount: ${newDataLength};`;
        }
      } else if ((getComputedStyle(document.querySelector('.liner')).width) === '1024px') {
        clipsPerPage = AppSlider.getResponceClipAmount();
        if (exClipsPerPage !== clipsPerPage) {
          reassignPage(clipsPerPage);
          leftClipIndex = getLeftClipIndex();
          exClipsPerPage = clipsPerPage;
          document.querySelector('.slider').style = `--clip-amount: ${newDataLength};`;
        }
      } else if ((getComputedStyle(document.querySelector('.liner')).width) === '768px') {
        clipsPerPage = AppSlider.getResponceClipAmount();
        if (exClipsPerPage !== clipsPerPage) {
          reassignPage(clipsPerPage);
          leftClipIndex = getLeftClipIndex();
          exClipsPerPage = clipsPerPage;
          document.querySelector('.slider').style = `--clip-amount: ${newDataLength};`;
        }
      } else if ((getComputedStyle(document.querySelector('.liner')).width) === '480px') {
        clipsPerPage = AppSlider.getResponceClipAmount();
        if (exClipsPerPage !== clipsPerPage) {
          reassignPage(clipsPerPage);
          leftClipIndex = getLeftClipIndex();
          exClipsPerPage = clipsPerPage;
          document.querySelector('.slider').style = `--clip-amount: ${newDataLength};`;
        }
      }
    };
  }
}
